import { Component, HostListener, OnInit } from '@angular/core';
import { LocalizeRouterService } from '@gilsdav/ngx-translate-router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { AnimationOptions } from 'ngx-lottie';
import { SCREEN_MD } from '../../constants';

@Component({
  selector: 'app-about-us',
  templateUrl: './about-us.component.html',
  styleUrls: ['./about-us.component.scss'],
})
export class AboutUsComponent implements OnInit {
  locales = this.localizeRouterService.parser.locales;
  currentUrl = '';
  isMobileOrTablet = true;
  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    if (event.target.innerWidth > SCREEN_MD) {
      this.isMobileOrTablet = false;
    } else {
      this.isMobileOrTablet = true;
    }
  }
  constructor(
    private deviceDetectorService: DeviceDetectorService,
    public localizeRouterService: LocalizeRouterService
  ) {}

  ngOnInit(): void {
    this.detectSize();
  }
  detectSize() {
    this.isMobileOrTablet =
      this.deviceDetectorService.isMobile() ||
      this.deviceDetectorService.isTablet();
  }
  options: AnimationOptions = {
    path: '/assets/lottie/stars.json',
  };
}
